.form.active {
  z-index: 9;
  background: #6c4a27;
  border: 6px solid #cb9a4b;
  width: 420px;
  height: 450px;
  padding: 24px;
  -webkit-animation: 1.4s cubic-bezier(.9, .1, .1, .9) openForm;
  animation: 1.4s cubic-bezier(.9, .1, .1, .9) openForm;
  display: block;
  position: absolute;
  top: 90%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 30px 20px -20px #0000004d;
}

.form.active:after, .form.active:before {
  opacity: 0;
}

.submit, .btn-close {
  opacity: 1;
  text-align: center;
  cursor: pointer;
  text-transform: uppercase;
  color: #3e451b;
  background: #d1db82;
  width: 160px;
  margin: 16px auto 0;
  padding: 8px;
  font-size: 16px;
  transition: all 1.1s 1s;
  top: 0;
  box-shadow: 0 6px 10px -4px #0000004d, inset 0 1px #ffffff80, inset 0 0 #00000080;
}

.form-input, .form-textarea {
  background: #fefefe;
  border: none;
  width: 100%;
  margin: 12px 0 0;
  padding: 10px;
  font-size: 16px;
  display: block;
  top: 0;
}

.input {
  top: 0;
}

.input:first-child {
  transition: all .5s 1s;
}

.input:nth-child(2) {
  transition: all .7s 1s;
}

.form-textarea {
  opacity: 1;
  max-height: 180px;
  font-size: 16px;
  transition: all .9s 1s;
  top: 0;
}

.form-head {
  opacity: 1;
  text-align: center;
  text-transform: uppercase;
  color: #cb9a4b;
  margin-bottom: 32px;
  font-size: 24px;
  transition: all .3s 1s;
  top: 0;
}

.form {
  cursor: pointer;
  background: #6c4a27;
  border: 6px solid #cb9a4b;
  width: 64px;
  height: 64px;
  display: none;
  position: absolute;
  top: 50%;
  right: 50%;
  overflow: hidden;
  box-shadow: 0 10px 20px -5px #0000004d;
}

.form:before, .form:after {
  content: "";
  opacity: 1;
  z-index: 100;
  width: 0;
  height: 0;
  transition: all .1s linear;
  display: inline-block;
  position: absolute;
  left: 10px;
  overflow: hidden;
  transform: scale(.9999);
}

.form:before {
  transform-origin: 50% 0;
  border: 16px solid #0000;
  border-top: 11px solid #fff;
  border-bottom-width: 11px;
  top: 15px;
  transform: perspective(1000px)rotateX(0);
}

.form:after {
  border: 16px solid #fff;
  border-top: 11px solid #0000;
  border-bottom-width: 11px;
  top: 17px;
}

.form:hover :before {
  transform-origin: 50% 0;
  transform: perspective(1000px)rotateX(180deg);
}

:after {
  top: 21px;
}

.ok_message {
  z-index: 100;
  text-align: center;
  cursor: pointer;
  background: #6c4a27;
  width: 64px;
  height: 64px;
  margin-top: -32px;
  margin-left: -32px;
  line-height: 64px;
  transition: all .5s cubic-bezier(.9, .1, .1, .9) .3s;
  top: 50%;
  left: 50%;
  transform: scale(0)rotate(-90deg);
}

.span {
  text-transform: uppercase;
  color: #cb9a4b;
  opacity: 0;
  font-size: 16px;
  transition: all .6s .4s;
  transform: scale(.2);
}

.span.active {
  width: 320px;
  margin-left: -160px;
  transform: scale(1)rotate(0);
}

span {
  opacity: 1;
  left: 0;
  transform: scale(1);
}

